	export default {
		data() {
			return {
                   goods:{},
                  reason:""   ,//退货原因
                  imgData:[],
                  arr:[],
                  tempFilePaths:[]
			}
		},
		methods: {
			addPhoto(index) {
				
				var _this=this;
				uni.chooseImage({
			
					sizeType: ['original', 'compressed'], //可以指定是原图还是压缩图，默认二者都有
					sourceType: ['album'], //从相册选择
					success: function(res) {
						_this.tempFilePaths = res.tempFilePaths.concat(_this.tempFilePaths);
						
						const uploadTask = uni.uploadFile({
							url: this.config.url+"/Upload",
							filePath: _this.tempFilePaths[0],
							name: 'file',
							formData: {
								'user': 'test'
							},
							success: function(uploadFileRes) {
							var a=JSON.parse(uploadFileRes.data)
							var b=a.data.url;
							   
								_this.arr.push(b)
								
								
							}
						});
			
			
					},
					error: function(e) {
						console.log(e);
					}
				});
			},
            applyReturn(order){
				this.request({
					url:this.config.url+"order/refund",
					method:"POST",
					data:{
						token:this.token,
						order_no:order,
						refund_type:1,
						refund_desc:this.reason,
						refund_covers:this.arr
						
					},
					success: (res) => {
						console.log("return",res)
						if(res.data.code==1){
							uni.showToast({
								title:res.data.info
							})
							uni.navigateTo({
								
						
							     url:"/pages/user/order_list/order_list"
							})
						}else{
							uni.showToast({
								title:res.data.info
							})
						}
					}
				})
			}
		},
		mounted() {
			uni.getStorage({
				key:"regoods",
				success:(res)=>{
					console.log("regoods",res)
					this.goods=res.data;
				}
			})
		},
		onLoad(e) {
			this.goods=JSON.parse(e.info)
		}
	}
